                            <p>Hoi nim v qu kh, nhng xu hng thi trang  v ang l cm hng ca rt nhiu b su tp thi trang ca cc nh thit k hin i.</p>
                            <div class="read-more">
                                <a href="javascript:;">Mua sm</a>
                            </div>
                        </div>
                      
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
export default {
  mounted() {
    /*----------------------------
 Product testimonial Carousel
------------------------------ */

    $(".total-product-testimonial").owlCarousel({
      autoPlay: true,
      slideSpeed: 2000,
      pagination: true,
      navigation: false,
      items: 1,
      navigationText: [
        "<i class='fa fa-angle-left'></i>",
        "<i class='fa fa-angle-right'></i>"
      ],
      itemsDesktop: [1199, 1],
      itemsDesktopSmall: [993, 1],
      itemsTablet: [767, 1],
      itemsMobile: [479, 1]
    });
  }
};
</script>



